body {
  height: 100%;
}
.filter {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border: solid red;
}

.filter__nav {
  height: 100vh;
  width: 15vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.nav__item {
  margin: 10px auto;
}

.nav__item--link {
  border: tomato solid;
  size: 10em;
}
.dropdown {
  border: springgreen solid;
  margin: auto;
  width: 50%;
}

.textarea {
  border: springgreen solid;
  margin: auto;
  width: 50%;
}
